import React, { Fragment, useState, useEffect } from 'react'
import MetaData from './layout/MetaData'
import { useDispatch, useSelector } from 'react-redux'
import { getProducts } from '../actions/productActions'
import Product from './product/Product'
import Loader from './layout/Loader'
import Pagination from 'react-js-pagination'
import { useParams } from 'react-router-dom';
import Slider from 'rc-slider';
import 'rc-slider/assets/index.css'
const createSliderWithTooltip = Slider.createSliderWithTooltip;
const Range = createSliderWithTooltip(Slider.Range)
const Home = () => {
    const dispatch = useDispatch();
    const { keyword } = useParams();
    const [currentPage, setCurrentPage] = useState(1)
    const [category, setCategory] = useState('')
    const [price, setPrice] = useState([1, 1000])
    const [rating, setRating] = useState(0)
    const { loading, productsCount, products,resPerPage,filteredProductsCount } = useSelector(state => state.products)
    const categories = 
    ['Electronics',
    'Cameras',
    'Laptops',
    'Accessories',
    'Food',
    'Book',
    'Headphones',
    'Clothes/Shoes',
    'Beauty/Health',
    'Sports',
    'Outdoor',
    'Home',
]
    useEffect(() => {
        dispatch(getProducts(keyword, currentPage, price,category,rating))
    }, [dispatch, keyword, currentPage, price,category,rating])
    function setCurrentPageNo(pageNumber) { 
       setCurrentPage(pageNumber)
    }
    let count=productsCount;
    if(keyword){
       count=filteredProductsCount;
    }

    return (
        <Fragment>
            {loading ? <Loader /> : (
                <Fragment>
                    <MetaData title={'Buy best products  online'}></MetaData>
                    <h1 id="products_heading">Latest Products</h1>


                    <section id="products" className="container mt-5">
                        <div className="row">
                            {keyword ?
                                <Fragment>
                                    <div className='col-6 col-md-3 mt-5 md-5'>
                                        <div className='px-5'> 
                                        <Range
                                        marks={{
                                            1:'$1',
                                            1000:'$1000'
                                        }}
                                        min={1}
                                        max={1000}
                                       
                                        defaultValue={[1,1000]}
                                        tipFormatter={value=>`$${value}`}
                                       

                                        tipProps={{
                                            placement:"top",
                                            visible:true
                                        }}
                                        value={price}
                                        onChange={price=>setPrice(price)}
                                        />

                                        <hr className='my-5'/>
                                        <div className='mt-5'>
                                            <h4 className='mb-3'>
                                                Categories
                                            </h4>
                                            <ul className='p1-0'>
                                                {categories.map(category=>(
                                                    <li 
                                                    style=
                                                    {{cursor:'pointer',
                                                    listStyleType:'none'}}
                                                    key={category}
                                                    onClick={()=>setCategory(category)}> 
                                                    {category}

                                                    </li>
                                                )

                                                )}
                                            </ul>
                                        </div>
                                        <hr className='my-3'/>
                                        <div className='mt-5'>
                                            <h4 className='mb-3'>
                                                Ratings
                                            </h4>
                                            <ul className='p1-0'>
                                                {[5,4,3,2,1].map(star=>(
                                                    <li 
                                                    style=
                                                    {{cursor:'pointer',
                                                    listStyleType:'none'}}
                                                    key={star}
                                                    onClick={()=>setRating(star)}> 
                                                    <div className='rating-outer'>
                                                        <div className='rating-inner' style={{width:`${star*20}%`}}>
                                                        {star}
                                                        </div>
                                                    
                                                    </div>
                                                  

                                                    </li>
                                                )

                                                )}
                                            </ul>
                                        </div>

                                        </div>
                                    </div>
                                    <div className='col-6 col-md-9'>
                                        <div className='row'>
                                            {
                                        products && products.map(product => (
                                    <Product key={product._id} product={product} col={4}></Product>
                                        
                                ))
                                        }
                                        </div>
                                    </div>
                                </Fragment>
                                : products && products.map(product => (
                                    <Product key={product._id} product={product} col={3}></Product>

                                ))}



                        </div>
                    </section>
                    <div className="d-flex justify-content-center mt-5">
                    {resPerPage <= count && (
                            <Pagination
                                activePage={currentPage}
                                itemsCountPerPage={resPerPage}
                                totalItemsCount={productsCount}
                                onChange={setCurrentPageNo}
                                nextPageText={'Next'}
                                prevPageText={'Prev'}
                                firstPageText={'First'}
                                lastPageText={'Last'}
                                itemClass="page-item"
                                linkClass="page-link"
                            />
                    )}
                    </div>

                </Fragment>
            )}


        </Fragment>
    )
}

export default Home
